<template>
  <v-dialog v-model="opened" max-width="600px">
    <v-toolbar dense>
      <v-toolbar-title>
        <v-icon large class="mt-2 mr-4" color="primary"> mdi-cog-refresh </v-icon>
      </v-toolbar-title>
      <v-spacer></v-spacer>
      <v-btn icon @click="$emit('update:dialog', false)" v-if="!disableClose">
        <v-icon large> $close </v-icon>
      </v-btn>
    </v-toolbar>

    <v-card flat class="homefone pa-8">
      <v-row align="center" justify="center">
        <v-card-text class="text-center">
          <p style="color: #777">
            <b>{{ address }}</b>
          </p>
        </v-card-text>
      </v-row>

      <v-row align="center" justify="center">
        <v-card-text class="text-center">
          <p><small>Service: </small><b>{{ serviceData.name }}</b></p>
        </v-card-text>
      </v-row>

      <v-row align="center" justify="center" v-if="!submited">
        <v-card-text class="text-center">
          <p><small>Current status: </small><b>{{ serviceData.status }}</b></p>
        </v-card-text>
      </v-row>

      <v-row align="center" justify="center" v-if="!submited">
        <v-card-text class="text-center">
          <p>
            <small>
              Would you like to request an update on the service delivery status?
            </small>
          </p>
        </v-card-text>
        <v-card-text class="text-center">
          <v-btn outlined color="primary" class="mr-2" @click="sendRequest">Yes</v-btn>
          <v-btn outlined color="primary" @click="reset">No</v-btn>
        </v-card-text>
      </v-row>

      <v-row align="center" justify="center" v-if="submited">
        <v-card-text class="text-center">
          <p>
            <small>
              You service delivery update request has been sent.
            </small>
          </p>
        </v-card-text>
      </v-row>
    </v-card>
  </v-dialog>
</template>

<script>

export default {
  name: 'ServiceDeliveryUpdate',
  props: ['dialog', 'address', 'serviceData', 'customerId'],
  data: () => ({
    status,
    disableClose: false,
    submited: false
  }),
  computed: {
    opened: {
      get () {
        return this.dialog
      },
      set (val) {
        this.$emit('update:dialog', val)
      }
    }
  },
  methods: {
    sendRequest () {
      this.disableClose = true
      this.submited = false
      this.__sendServiceActivationRequest({ customerId: this.customerId, serviceId: this.serviceData.id })
    },
    getResponse (event) {
      this.disableClose = false
      this.submited = true
      console.log(event)
      this.$emit('update:serviceData', event)
      this.opened = false
    },
    reset () {
      this.opened = false
    }
  },

  beforeDestroy () {
    this.$root.$off('service-activation-request-sent', this.getResponse)
  },

  mounted () {
    console.log('SERVICE DETAILS\n', this.serviceData)
    this.$root.$on('service-activation-request-sent', this.getResponse)
  }
}
</script>
